"use client"

import { useEffect, useState } from "react"
import { AdminLayout } from "@/components/admin-layout"
import { StatsCard } from "@/components/stats-card"
import { UsersTable } from "@/components/users-table"
import { Users, DollarSign, TrendingUp } from "lucide-react"
import { useAuth } from "@/context/AuthContext"
import { User } from "@/lib/mock-data" // Keep User interface for type checking

export default function DashboardPage() {
  const { isAuthenticated } = useAuth()
  const [users, setUsers] = useState<User[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const totalRevenue = 480 // Mock revenue, to be replaced later
  const activeGames = 2 // Mock active games, to be replaced later

  useEffect(() => {
    if (!isAuthenticated) {
      setLoading(false)
      return // Don't fetch if not authenticated
    }

    const fetchUsers = async () => {
      try {
        const token = localStorage.getItem("sb_admin_token")
        if (!token) {
          throw new Error("Authentication token not found.")
        }

        const res = await fetch("/api/admin/users", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })

        if (!res.ok) {
          const errorData = await res.json()
          throw new Error(errorData.error || "Failed to fetch users.")
        }

        const data: User[] = await res.json()
        setUsers(data)
      } catch (err: any) {
        setError(err.message)
        console.error("Error fetching users:", err)
      } finally {
        setLoading(false)
      }
    }

    fetchUsers()
  }, [isAuthenticated])

  if (loading) {
    return (
      <AdminLayout>
        <div className="flex items-center justify-center min-h-[calc(100vh-4rem)]">
          <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin"></div>
        </div>
      </AdminLayout>
    )
  }

  if (error) {
    return (
      <AdminLayout>
        <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] text-destructive">
          Error: {error}
        </div>
      </AdminLayout>
    )
  }

  return (
    <AdminLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-foreground">Dashboard</h1>
          <p className="text-muted-foreground">Welcome back, Admin</p>
        </div>

        <div className="grid gap-4 md:grid-cols-3">
          <StatsCard title="Total Users" value={users.length} icon={Users} trend="+12% from last month" />
          <StatsCard title="Revenue" value={`$${totalRevenue}`} icon={DollarSign} trend="+8% from last month" />
          <StatsCard title="Active Games" value={activeGames} icon={TrendingUp} trend="2 games running" />
        </div>

        <UsersTable users={users} title="Recent Users" />
      </div>
    </AdminLayout>
  )
}
